import { Component, OnInit } from "@angular/core";
import { BroadcastService, MsalService } from "@azure/msal-angular";
import { Logger, CryptoUtils } from "msal";
import { HttpClient, HttpHeaders } from "@angular/common/http";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.css"],
})
export class AppComponent implements OnInit {
  title = "HR ADMIN DASHBOARD";
  loggedIn = false;

  constructor(
    private http: HttpClient,
    private broadcastService: BroadcastService,
    private authService: MsalService
  ) {}

  ngOnInit() {
    this.checkAccount();

    this.broadcastService.subscribe("msal:loginSuccess", (payload) => {
      console.log("login success");
      console.log(payload);
      this.checkAccount();
      this.getProfile();
    });

    this.broadcastService.subscribe("msal:acquireTokenSuccess", (payload) => {
      console.log("msal:msal:acquireTokenSuccess");
      console.log(payload);
    });

    this.authService.handleRedirectCallback((authError, response) => {
      if (authError) {
        console.error("Redirect Error: ", authError.errorMessage);
        return;
      }

      console.log("Redirect Success: ", response.accessToken);
    });

    this.authService.setLogger(
      new Logger(
        (logLevel, message, piiEnabled) => {
          console.log("MSAL Logging: ", message);
        },
        {
          correlationId: CryptoUtils.createNewGuid(),
          piiLoggingEnabled: false,
        }
      )
    );
  }

  checkAccount() {
    console.log("check Account");
    this.loggedIn = !!this.authService.getAccount();
  }

  login() {
    console.log("login");
    this.authService.loginPopup();
  }

  logout() {
    this.authService.logout();
  }

  getProfile() {
    console.log("getProfile");
    const graphMeEndpoint = "https://graph.microsoft.com/v1.0/me";
    this.authService
      .acquireTokenSilent({ scopes: ["user.read"] })
      .then((tokenResponse) => {
        console.log("token response is:", tokenResponse);
        let { accessToken } = tokenResponse;
        let headers = new HttpHeaders({
          "Content-Type": "application/json",
          Authorization: "Bearer " + accessToken,
        });
        this.http
          .get(graphMeEndpoint, { headers })
          .toPromise()
          .then((profile) => {
            console.log("getProfile response", profile);
          });
      });
  }
}
